.product-card__wrapper {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.product-card__header {
  width: 100%;
  max-width: 160px;
  height: 134px;
}

.product-card__header:not(:last-child) {
  margin-bottom: 40px;
}

.product-card__img {
  object-position: center;
  width: 100%;
  height: 100%;
}

.product-card__main:not(:last-child) {
  margin-bottom: 16px;
}

.product-card__footer {
  width: 100%;
  max-width: 100%;
}

.product-card__link {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 100%;
  padding: 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: color .3s, background-color .3s, border .3s;
  display: flex;
}

.product-card__link:hover {
  color: #00acdc;
  background-color: #0000;
  border-color: #00acdc;
}

.product-info__titles:not(:last-child) {
  margin-bottom: 15px;
}

.product-info__title {
  font-size: inherit;
  color: #060b35;
  font-weight: 500;
}

.product-info__title:not(:last-child) {
  margin-bottom: 4px;
}

.product-info__subtitle {
  font-size: 10px;
  font-weight: inherit;
  line-height: 14px;
}

.product-info__block {
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  display: flex;
}

.product-info__price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.reviews {
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  display: flex;
}

.reviews:not(:last-child) {
  margin-bottom: 23px;
}

.reviews__text {
  font-size: 10px;
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  color: #616070;
  justify-content: center;
  font-family: Roboto, sans-serif;
